.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(16px + 2vmin);
  color: white;
  min-width: 100px;
}

.character-form{
  margin: 8px, 8px;
}


label.lb-label {
  margin-right: 4pt;
  display: inline-block;
}

.input{
  width: 25%;
  padding: 12px 20px;
  margin: 8px 8px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  height: 20 px;
}

.input-box > .character-form > input, 
.input-box > .character-form > select{
  font-size: 24px;
  margin: 4px;
  border-radius: 0.4rem;
}

.mobile-input-box > .character-form{
  font-size: 14px;
}

.mobile-input-box > .character-form > input, 
.mobile-input-box > .character-form > select{
  font-size: 9px;
  margin: 4px;
  border-radius: 0.4rem;
}

input.levelbox{
  border: none;
}

.button-inactive{
  /* background-color: gray;
  color: darkgray;
  border: none; */
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
  pointer-events: none;
}

.stats, .stats-mobile{
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}

.stats > div, .stats-mobile > div {
  display: inline-block;
  *display: inline;
  font-size: 40pt;
  text-align: center;
  width: 16%;
}

/* Desktop CSS */
.stats > .stat-block > .lab-str {
  font-size: 20pt;
  color: #ff9aa2;
}
.stats > .stat-block > .lab-dex {
  font-size: 20pt;
  color: #ffb7b2;
}
.stats > .stat-block > .lab-con {
  font-size: 20pt;
  color: #ffdac1;
}
.stats > .stat-block > .lab-int {
  font-size: 20pt;
  color: #e2f0cb;
}
.stats > .stat-block > .lab-wis {
  font-size: 20pt;
  color: #b5ead7;
}
.stats > .stat-block > .lab-cha{
  font-size: 20pt;
  color: #c7ceea;
}
.stats > .stat-block > .lab-health{
  font-size: 20pt;
  color: lightgray;
}

/* Mobile CSS */

.stats-mobile > div {
  font-size: 30pt;
}

.stats-mobile > .stat-block > .lab-str {
  font-size: 16pt;
  color: #ff9aa2;
}
.stats-mobile > .stat-block > .lab-dex {
  font-size: 16pt;
  color: #ffb7b2;
}
.stats-mobile > .stat-block > .lab-con {
  font-size: 16pt;
  color: #ffdac1;
}
.stats-mobile > .stat-block > .lab-int {
  font-size: 16pt;
  color: #e2f0cb;
}
.stats-mobile > .stat-block > .lab-wis {
  font-size: 16pt;
  color: #b5ead7;
}
.stats-mobile > .stat-block > .lab-cha{
  font-size: 16pt;
  color: #c7ceea;
}
.stats-mobile > .stat-block > .lab-health{
  font-size: 16pt;
  color: lightgray;
}


